import React from 'react';
import { Link } from 'react-router-dom';
//import './App.css';
import './Bustickets.css';
import Busdetails from './Busdetails';



class Bustickets extends React.Component {
    render() {
        return (<div className="redbus" >
            <div className="navbar">
                <Link className="bussize" to="/">
                    <img className="redbuslogo" src="https://cdn-images-1.medium.com/max/422/1*SHy-CTsuk8l70UbhTf67PQ@2x.png" />
                </Link>
                <Link to="./official">
                    <a className="a1" href="#bus">BUS TICKETS</a>
                </Link>
                <a className="a2" href="#home">HOTELS</a>
                <a className="a3" href="#bus">BUS HIRE</a>
                <a className="a4" href="#home">PILGRIMAGES</a>
                <a className="a5" href="#bus">CARS</a>
                <a className="a6" href="#home">outstation</a>
                <a className="a7" href="#home">Help</a>
                <a className="a8" href="#bus">print/SMS Ticket</a>
                <span className="down"><img className="arrow1" src={require('./img1/downarrow.svg')}></img></span>
                <span>
                    <button onClick={() => document.getElementById('id01').style.display = 'block'}><img className="user" src={require('./img1/userprofile.svg')} /></button>
                    <div id="id01" class="modal">
                        <form class="modal-content animate" action="/action_page.php">
                            <div class="imgcontainer">
                                <span onClick={() => document.getElementById('id01').style.display = 'none'} class="close" title="Close Modal">&times;</span>
                            </div>
                            <div className="loginimage">
                                <div className="earn" />
                            </div>
                            <div className="logindetails">
                                <div className="loginbus"></div>
                                <p className="sign">SIGN IN / SIGN UP</p>
                                <div className="box1">
                                    <div className="mobilebox">
                                        <input type="number" class="IP" placeholder="Enter your mobile number" id="mobileNoInp" data-validate="required|internationalphone" data-message="Please enter valid mobile number|Please enter valid mobile number"></input>
                                        <ul id="selectedPhCode" data-cntrycode="IND" class="select_input_code">+ 91</ul><img className="angledown" src={require('./img1/blackarrow.svg')}></img><span className="phnoplaceholder">
                                        </span>
                                    </div>
                                </div>
                                <button class="generateotp">Generate OTP</button>
                                <div className="fbbtn">
                                    <button className="facebookbutton">Sign In With Facebook</button>
                                </div>
                                <div className="googlebtn">
                                    <div className="googlebackgrnd">
                                        <img className="goole" src={require('./img1/google.svg')}></img>
                                    </div>
                                    <button className="googlebutton">Sign In With Google</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </span>
                <span className="up"><img className="arrow2" src={require('./img1/downarrow.svg')}></img></span>
            </div>
            <div className="sidenav">
                <div className="navtitle">FILTERS</div>
                <div className="livetracking">
                    <i class="fas fa-map-marker-alt"></i><span className="tracking">Live Tracking (102)</span><i class="fas fa-check ckeck1"></i>
                </div>
                <div className="reddeals">
                    <i class="fas fa-tag"></i><span className="deals">Red Deals (25)</span><i class="fas fa-check check2"></i>
                </div>
                <div className="Reschedulable">
                    <i class="fas fa-history"></i><span className="schedulable">Reschedulable (41)</span><i class="fas fa-check check3"></i>
                </div>
            </div>
            <div className="travellinfo">
                <span className="element1">119 Buses</span><span className="element2"> found</span><span className="element3">SORT BY :</span><span className="element4">Departure</span><span className="element5">Duration</span><span className="element6">Arrival</span><span className="element7">Ratings</span><span className="element8">Fare</span><span className="element9">Seats Available</span>
            </div>
            <div>
                {/* <nav>
                    <h2>My Header</h2>
                </nav> */}
                {/* <div class="header sticky" id="myHeader">
                    <h2>My Header</h2>
                </div> */}


            </div>
            <Busdetails />
        </div>
        );

    }
}

export default Bustickets;